<template>
  <q-layout view="LHh lpR lFf" class="bg-color">
    <q-page-container>
      <q-page padding class="container bg-color flex flex-center q-pb-xl">
        <div
          v-if="!request_submitted"
          class="absolute-top-left q-mt-lg q-pl-md"
        >
          <q-btn
            outline
            no-caps
            unelevated
            :ripple="{ color: 'accent' }"
            color="primary"
            class="rounded slide-hover full-width button-gm-outline-dark text-body1 font-medium button-md"
            to="/"
          >
            <q-icon size="20px" name="icon-arrow-narrow-left" class="q-pr-xs" />
            Back to Growmodo
          </q-btn>
        </div>

        <div style="width: 696px; max-width: 696px">
          <div
            v-if="!request_submitted"
            class="text-h2 text-center font-medium text-primary q-pt-lg q-pb-xl letter-space-negative"
          >
            Request a {{ isTask ? 'Task' : 'Project' }}
          </div>
          <q-card
            bordered
            class="rounded shadows-sm card-padding-lg full-width"
          >
            <q-form
              autocorrect="off"
              autocapitalize="off"
              autocomplete="off"
              spellcheck="false"
              @submit.prevent
            >
              <div class="row q-col-gutter-md">
                <div v-if="request_submitted" class="col-12">
                  <!-- Done -->
                  <div class="text-center">
                    <CardIconBox icon="icon-check-circle-broken" />
                  </div>
                  <div
                    class="full-width font-medium text-h4 q-pt-lg text-center"
                  >
                    Thank you.<br />Your {{ isTask ? 'task' : 'project' }} have
                    been successfully submitted.
                  </div>
                </div>

                <div v-else-if="showSkeleton" class="col-12">
                  <!-- Loading -->
                  <q-skeleton type="QToolbar" width="70%" />
                  <q-skeleton width="45%" type="QBadge" class="q-my-md" />
                  <div class="row q-col-gutter-md">
                    <div class="col-6" v-for="j in [1, 2, 3, 4]" :key="j">
                      <q-skeleton type="QInput" />
                    </div>
                  </div>
                </div>

                <template v-else>
                  <div
                    v-for="(question, i) in questions"
                    :key="question"
                    :name="i"
                    class="col-12 row q-col-gutter-sm"
                  >
                    <div class="col-12 text-body2 text-primary-500 font-medium">
                      {{ question.title }}
                    </div>
                    <div class="col-12">
                      <TextArea1
                        v-if="question.type === 'textarea1'"
                        :autofocus="false"
                        :dense="false"
                        v-model="answer[question.id].value"
                        :required="question.required"
                        :name="'question-' + question.id"
                        placeholder="Enter a description..."
                        rows="5"
                      />
                      <TextArea2
                        v-else-if="question.type === 'textarea2'"
                        :autofocus="false"
                        :dense="false"
                        v-model="answer[question.id].value"
                        :required="question.required"
                        :name="'question-' + question.id"
                        placeholder="Enter a description..."
                        rows="5"
                      />
                      <InputTextField
                        v-else-if="question.type === 'textfield'"
                        :autofocus="false"
                        :dense="false"
                        v-model="answer[question.id].value"
                        :name="'question-' + question.id"
                        placeholder="Your target audience"
                        :required="question.required"
                      />
                      <SelectOption
                        v-else-if="question.type === 'select'"
                        :autofocus="false"
                        :dense="false"
                        :initModel="answer[question.id].value"
                        v-model="answer[question.id].value"
                        :selection="question.options"
                        :required="question.required"
                        :name="'question-' + question.id"
                      />
                      <SelectOption
                        v-else-if="question.type === 'select_multiple'"
                        :autofocus="false"
                        :dense="false"
                        multiple
                        :initModel="answer[question.id].value"
                        v-model="answer[question.id].value"
                        :selection="question.options"
                        :required="question.required"
                        :name="'question-' + question.id"
                      />
                      <RadioSelect
                        v-else-if="question.type === 'radio'"
                        :autofocus="false"
                        :dense="false"
                        :initModel="answer[question.id].value"
                        :selection="question.options"
                        :required="question.required"
                        :name="'question-' + question.id"
                        @updated="(v) => (answer[question.id].value = v)"
                      />
                      <CheckboxSelect
                        v-else-if="question.type === 'checkbox'"
                        :autofocus="false"
                        :dense="false"
                        :initModel="answer[question.id].value"
                        :selection="question.options"
                        :required="question.required"
                        :name="'question-' + question.id"
                        @updated="(v) => (answer[question.id].value = v)"
                      />
                      <SelectPlatforms
                        v-else-if="question.type === 'select_platform'"
                        :autofocus="false"
                        :dense="false"
                        :initModel="answer[question.id].value"
                        :required="question.required"
                        :platforms="question.options"
                        :name="'question-' + question.id"
                        @updated="(v) => (answer[question.id].value = v)"
                      />
                      <InputVideoWalkthrough
                        v-else-if="question.type === 'video_walkthrough'"
                        :autofocus="false"
                        :dense="false"
                        class="full-width"
                        :name="'question-' + question.id"
                        :initModel="answer[question.id].value"
                        :required="question.required"
                        @updated="(v) => (answer[question.id].value = v)"
                      />
                      <SelectBrand
                        v-else-if="question.type === 'select_brand'"
                        :autofocus="false"
                        :dense="false"
                        v-model="answer[question.id].value"
                        :initModel="answer[question.id].value"
                        :name="'question-' + question.id"
                        :required="question.required"
                        @brand-added="(e) => (answer[question.id].value = e)"
                        @brand-finished="(e) => (answer[question.id].value = e)"
                        @brand-form-closed="
                          () => {
                            if (
                              answer[question.id].value &&
                              answer[question.id].value?.id === 0
                            )
                              answer[question.id].value = '';
                          }
                        "
                      />
                      <SelectTaskProjectType
                        v-else-if="question.type === 'select_tasktype'"
                        :autofocus="false"
                        :dense="false"
                        type="task"
                        v-model="answer[question.id].value"
                        :initModel="answer[question.id].value"
                        :required="question.required"
                      />
                      <SelectTaskProjectType
                        v-else-if="question.type === 'select_projecttype'"
                        :autofocus="false"
                        :dense="false"
                        type="project"
                        v-model="answer[question.id].value"
                        :initModel="answer[question.id].value"
                        :required="question.required"
                      />
                      <SelectCategoryType
                        v-else-if="question.type === 'select_taskcategory'"
                        :autofocus="false"
                        :dense="false"
                        type="task"
                        v-model="answer[question.id].value"
                        :initModel="answer[question.id].value"
                        :required="question.required"
                      />
                      <SelectCategoryType
                        v-else-if="question.type === 'select_projectcategory'"
                        :autofocus="false"
                        :dense="false"
                        type="project"
                        v-model="answer[question.id].value"
                        :initModel="answer[question.id].value"
                        :required="question.required"
                      />
                      <InputWebsite
                        v-else-if="question.type === 'url'"
                        :autofocus="false"
                        :dense="false"
                        v-model="answer[question.id].value"
                        :required="question.required"
                      />
                      <InputPhoneNumber
                        v-else-if="question.type === 'phone'"
                        :autofocus="false"
                        :dense="false"
                        :required="question.required"
                        @areaCodeChanged="
                          (e) => {
                            answer[question.id].value = answer[question.id]
                              .answer
                              ? `${e}+${
                                  answer[question.id].value?.split('+')[1] || ''
                                }`
                              : e;
                          }
                        "
                        @numberChanged="
                          (e) => {
                            answer[question.id].value = answer[question.id]
                              .answer
                              ? `${
                                  answer[question.id].value?.split('+')[0] || ''
                                }+${e}`
                              : e;
                          }
                        "
                        :areaCode="answer[question.id].value.split('+')[0]"
                        :phoneNumber="answer[question.id].value.split('+')[1]"
                      />
                      <FileUploader
                        v-else-if="
                          question.type === 'upload_single' ||
                          question.type === 'upload_multiple'
                        "
                        :autofocus="false"
                        :dense="false"
                        :multiple="question.type === 'upload_multiple'"
                        temporary
                        withItems
                        miniItems
                        :ref="`upload${question.id}`"
                        max-file-size="10000000"
                        :note="question?.options?.note || ''"
                        :accept="
                          question?.options?.accept ||
                          'image/*, woff, woff2, otf, ttf, psd, eps, ai, xd, fig'
                        "
                        :removedFile="
                          uploads[`upload${question.id}`]?.removedFile
                        "
                        @added="
                          (files) => {
                            if (
                              !uploads[`upload${question.id}`]?.files?.length ||
                              question.type === 'upload_multiple'
                            ) {
                              addUploadToModel(
                                'uploads',
                                `upload${question.id}`,
                                files,
                                `upload${question.id}`
                              );
                            } else {
                              uploads[`upload${question.id}`].files = files;
                            }
                          }
                        "
                        @uploaded="
                          ({ files, xhr }) => {
                            const response = JSON.parse(xhr.response || '{}');
                            if (response.success) {
                              const data = response.data;
                              if (question.type === 'upload_multiple') {
                                answer[question.id].value = answer[
                                  question.id
                                ].concat(data.map((e) => e.id));
                              } else {
                                answer[question.id].value = data[0]?.id;
                              }
                            }
                          }
                        "
                        @removed="
                          (files) => {
                            if (question.type === 'upload_multiple') {
                              files
                                .map((e) => e.id)
                                .forEach((e) => {
                                  const ind = answer[
                                    question.id
                                  ].answer.findIndex((a) => a === e);
                                  if (ind !== -1) {
                                    answer[question.id].value.splice(ind, 1);
                                  }
                                });
                            } else {
                              answer[question.id].value = '';
                            }
                          }
                        "
                        :factory="factoryFn"
                        :initSlots="uploads[`upload${question.id}`]"
                        thumbnail="auto"
                      />
                    </div>
                  </div>
                </template>
                <template v-if="!request_submitted">
                  <div class="col-12">
                    <div class="row items-center">
                      <q-icon
                        name="icon-alert-circle"
                        size="sm"
                        class="q-pr-sm"
                        color="warning-500"
                      />
                      Turnaround time might be longer if the brief is
                      incomplete.
                    </div>
                  </div>
                  <div class="col-12">
                    <div class="row items-center">
                      <q-btn
                        no-caps
                        unelevated
                        color="primary"
                        :disable="showSkeleton || request_submitting"
                        class="full-width text-body1 font-medium button-lg"
                        @click="submitRequest"
                      >
                        Submit Request
                        <q-icon
                          size="20px"
                          name="icon-arrow-narrow-right"
                          class="q-pl-xs"
                        />
                      </q-btn>
                    </div>
                  </div>
                </template>
                <template v-else>
                  <div class="col">
                    <q-btn
                      outline
                      no-caps
                      :ripple="{ color: 'accent' }"
                      color="primary"
                      class="rounded slide-hover full-width text-body1 font-medium button-gm-outline-dark button-lg"
                      @click="$root.goTo_Home"
                    >
                      <q-icon
                        size="20px"
                        name="icon-arrow-narrow-left"
                        class="q-pr-sm"
                      />
                      Back to home
                    </q-btn>
                  </div>
                  <div class="col">
                    <q-btn
                      no-caps
                      unelevated
                      :ripple="{ color: 'accent' }"
                      color="primary"
                      class="rounded slide-hover full-width text-body1 font-medium button-lg"
                      @click="goTo_Directory()"
                    >
                      Add your next request
                    </q-btn>
                  </div>
                </template>
              </div>
            </q-form>
          </q-card>
        </div>
      </q-page>
    </q-page-container>
  </q-layout>
</template>

<script>
import { defineComponent, defineAsyncComponent, ref } from 'vue';
import { mapState, mapActions } from 'pinia';
import useGrowmodo from 'stores/growmodo';
import useApp from 'stores/app';
import { extractErrorMessageString } from 'src/assets/scripts/functions';
import { checkIfArray } from 'src/assets/scripts/functions';

export default defineComponent({
  name: 'QuickRequest',

  components: {
    SelectBrand: defineAsyncComponent(() =>
      import('src/components/Brands/SelectBrand.vue')
    ),
    SelectPlatforms: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectPlatforms.vue')
    ),
    RadioSelect: defineAsyncComponent(() =>
      import('src/components/Helpers/RadioSelect.vue')
    ),
    CheckboxSelect: defineAsyncComponent(() =>
      import('src/components/Helpers/CheckboxSelect.vue')
    ),
    InputTextField: defineAsyncComponent(() =>
      import('src/components/Helpers/InputTextField.vue')
    ),
    TextArea1: defineAsyncComponent(() =>
      import('src/components/Helpers/TextArea1.vue')
    ),
    TextArea2: defineAsyncComponent(() =>
      import('src/components/Helpers/TextArea2.vue')
    ),
    InputVideoWalkthrough: defineAsyncComponent(() =>
      import('src/components/Helpers/InputVideoWalkthrough.vue')
    ),
    SelectOption: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectOption.vue')
    ),
    InputWebsite: defineAsyncComponent(() =>
      import('src/components/Helpers/InputWebsite.vue')
    ),
    InputPhoneNumber: defineAsyncComponent(() =>
      import('src/components/Helpers/InputPhoneNumber.vue')
    ),
    FileUploader: defineAsyncComponent(() =>
      import('src/components/Helpers/FilesUploader.vue')
    ),
    SelectCategoryType: defineAsyncComponent(() =>
      import('/src/components/Helpers/SelectCategoryType.vue')
    ),
    CardIconBox: defineAsyncComponent(() =>
      import('src/components/Helpers/CardIconBox.vue')
    ),
    SelectTaskProjectType: defineAsyncComponent(() =>
      import('src/components/Helpers/SelectTaskProjectType.vue')
    ),
  },

  data() {
    return {
      request: {},
      questions: ref([]),
      answer: ref({}),
      request_submitting: false,
      request_submitted: false,
    };
  },

  computed: {
    ...mapState(useGrowmodo, ['taskDirectory', 'projectDirectory']),

    title() {
      return `Quick Request - ${this.isTask ? 'Task' : 'Project'}`;
    },

    type() {
      return this.$route.params?.quickRequestType || 'task';
    },

    isTask() {
      return this.type.toLowerCase() === 'task' ? true : false;
    },

    showSkeleton() {
      return (
        !this.questions.length ||
        Object.keys(this.answer).length !== this.questions.length
      );
    },

    typeId() {
      const id = this.questions.findIndex((e) => e.type == 'select_tasktype');
      return this.questions[id];
    },

    typeanswer() {
      return this.answer[this.typeId?.id];
    },

    requestType() {
      return this[this.isTask ? 'taskDirectory' : 'projectDirectory'][
        this.typeanswer?.value
      ];
    },
  },

  async mounted() {
    this.$root.setMeta({ title: this.title });
    const response = this.isTask ? await this.getTaskQuickRequest() : {};
    this.questions = response?.data?.dynamic_questions || [];
    await this.init_declare();
    this.init();
  },

  methods: {
    ...mapActions(useGrowmodo, [
      'getTaskQuickRequest',
      'requestTask',
      'requestProject',
    ]),
    ...mapActions(useApp, ['init_RequestForm']),

    async init_declare() {
      this.answer = {};
      this.request_submitting = false;
      this.request_submitted = false;
      this.uploads = {};
    },

    async submitRequest() {
      this.request_submitting = true;
      const payload = {
        title: `${this.requestType?.title}`,
        [`${this.isTask ? 'task' : 'project'}_type_id`]: this.requestType?.id,
      };
      const dynamic_questions = [];
      for (const i of Object.keys(this.answer)) {
        dynamic_questions.push({
          id: i,
          answer: checkIfArray(this.answer[i])
            ? this.answer[i]
            : [this.answer[i]],
        });
      }
      payload.dynamic_questions = dynamic_questions;
      const response = this.isTask
        ? await this.requestTask(payload)
        : await this.requestProject(payload);
      const message = extractErrorMessageString(response?.message);
      if (response.success) {
        if (message)
          this.$q.notify({
            icon: 'icon-check-circle-broken',
            message: message,
          });
        this.request_submitted = true;
      } else {
        this.$q.notify({
          icon: 'icon-alert-triangle',
          iconColor: 'negative',
          message: message || 'Request submittion failed!',
        });
      }
      this.request_submitting = false;
    },

    async init() {
      this.init_RequestForm();
      for (const q of this.questions) {
        let ans = { value: '', alternative_answer: '' };
        // Answer
        switch (q.type) {
          case 'url':
          case 'phone':
          case 'textarea1':
          case 'textarea2':
          case 'textfield':
          case 'select_brand':
          case 'video_walkthrough':
          case 'upload_single':
          case 'select_tasktype':
            ans.value = '';
            break;

          case 'select_multiple':
          case 'checkbox':
          case 'upload_multiple':
            ans.value = [];
            break;

          case 'radio':
          case 'select_platform':
            ans.value = {};
            break;

          default:
            break;
        }
        // Alternative Answer
        switch (q.alternative_type) {
          case 'url':
          case 'phone':
          case 'textarea1':
          case 'textarea2':
          case 'textfield':
          case 'select_brand':
          case 'video_walkthrough':
          case 'upload_single':
            ans.alternative_answer = '';
            break;

          case 'select_multiple':
          case 'checkbox':
          case 'upload_multiple':
            ans.alternative_answer = [];
            break;

          case 'radio':
          case 'select_platform':
            val.alternative_answer = {};
            break;

          default:
            break;
        }

        this.answer[q.id] = ans;
      }
    },

    goTo_Directory(model) {
      if (!model) model = this.isTask ? 'task' : 'project';
      // Open Directory
      this.$root.toggleRequest(model == 'task' ? 'project' : 'task', false);
      this.$root.toggleRequest(model, true);
    },
  },
});
</script>
